body {
  background-color: grey;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.img-small {
    margin-bottom: 10px;
  }
  
  img {
    width: 150px;
    margin-left: 2rem;
    margin-right: 2rem
  }
.wrapper {
  padding-top: 10px;
  background-color: grey;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding-left: 27%;
  padding-right: 27%;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}

.title {
    width: 100%;
    text-align: center;
    font-size: 2.5rem;
    margin-bottom: 2%;
  }
.score {
    width: 100%;
    text-align: center;
    font-size: 1.5rem;
    margin-bottom: 2rem;
  }
